import Vue from 'vue';
import Vonic from 'vonic/src/index.js';
// Page Components
import index from './page/home/order.vue';
//
import orderDetial from './page/order/orderDetial.vue';
// Routes
const routes = [
    { path: '/', component: index },
    { path: '/order/detial', component: orderDetial }
]
// Vonic.app.setConfig('pushMethod', 'replace');
Vue.use(Vonic.app, {
    routes: routes,
    pushMehod: 'push'
})


